@import "@styles/variables.scss";

.button {
    height: 30px;
    width: 30px;
    margin: 10px;
    padding: 5px;
    background: $counter_color;
    cursor: pointer;
    display: flex;
    border: 0;
    justify-content: center;
    align-items: center;
    border-radius: 25%;
    transition: all 0.3s;

    &:hover {
        transition: all 0.3s;
        box-shadow: 0px 1px 2px 1px rgba(0, 0, 0, 0.3);
    }
}

.dots_style {
    height: 30px;
    border: 0;
    background: white;
    width: 30px;
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: default;
    color: red;
}

.button_text {
    @include block_main_font;
}

.active {
    color: $loader_color;
}

@media screen and (max-width: 800px) {
    .button {
        font-size: 12px;
        width: 25px;
        height: 25px;
        margin: 10px;
        padding: 5px;
    }

    .dots_style {
        width: 15px;
    }
}
